import React, { useState, useEffect } from 'react';
import { useParams, Link } from 'react-router-dom';
import {
  Typography, Box, Button, Paper, Divider, Alert, Breadcrumbs, Skeleton, Stack
} from '@mui/material';
import { Home as HomeIcon } from '@mui/icons-material';
import { loadBookmarkletData } from '../utils/bookmarkletLoader';

const BookmarkletPage = () => {
  const { id } = useParams();
  const [bookmarklet, setBookmarklet] = useState(null);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const data = await loadBookmarkletData();
        const foundBookmarklet = data.find(b => b.id === id);
        setBookmarklet(foundBookmarklet || null);
      } catch (err) {
        setError('Failed to load bookmarklet details');
        console.error('Error loading bookmarklet details:', err);
      } finally {
        setLoading(false);
      }
    };
    fetchData();
  }, [id]);

  if (loading) {
    return (
      <Box sx={{ py: 4 }}>
        <Skeleton variant="text" width="40%" height={40} />
        <Skeleton variant="rectangular" height={200} sx={{ mt: 2 }} />
        <Skeleton variant="rectangular" height={150} sx={{ mt: 2 }} />
      </Box>
    );
  }

  if (error || !bookmarklet) {
    return (
      <Box sx={{ py: 4 }}>
        <Alert severity="error">{error || 'Bookmarklet not found'}</Alert>
        <Button component={Link} to="/" sx={{ mt: 2 }} aria-label="Back to home">
          Back to Home
        </Button>
      </Box>
    );
  }

  return (
    <Box sx={{ py: 4 }}>
      <Breadcrumbs aria-label="breadcrumb" sx={{ mb: 2 }}>
        <Link to="/" style={{ display: 'flex', alignItems: 'center' }}>
          <HomeIcon sx={{ mr: 0.5 }} /> Home
        </Link>
        <Typography color="text.primary">{bookmarklet.name}</Typography>
      </Breadcrumbs>
      <Paper elevation={3} sx={{ p: 3, mb: 3 }}>
        <Typography variant="h4" component="h1" gutterBottom>
          {bookmarklet.icon} {bookmarklet.name}
        </Typography>
        <Typography variant="body1" paragraph>
          {bookmarklet.description}
        </Typography>
        <Stack direction="row" spacing={2} sx={{ my: 3 }}>
          <Button
            component="a"
            href={bookmarklet.code}
            variant="contained"
            color="primary"
            draggable="true"
            size="large"
            aria-label={`Drag ${bookmarklet.name} to bookmarks bar`}
          >
            Drag to Bookmarks
          </Button>
          <Button variant="outlined" disabled>
            Preview (Coming Soon)
          </Button>
        </Stack>
      </Paper>
      <Paper elevation={3} sx={{ p: 3 }}>
        <Typography variant="h5" gutterBottom>
          Installation Instructions
        </Typography>
        <Divider sx={{ mb: 2 }} />
        <Typography variant="body1" paragraph>
          1. Show your bookmarks bar (Ctrl+Shift+B or Command+Shift+B).
        </Typography>
        <Typography variant="body1" paragraph>
          2. Drag the button above to your bookmarks bar.
        </Typography>
        <Typography variant="body1" paragraph>
          3. Click the bookmarklet on any relevant page.
        </Typography>
      </Paper>
    </Box>
  );
};

export default BookmarkletPage;